.cartDiv {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 1rem 0;
}
.cartDiv h1 {
    font-size: 2rem;
    letter-spacing: 0.1rem;
}
.cartDiv div div {
    display: flex;
    column-gap: 2rem;
}
.cartDiv p {
    font-size: 1rem;
}
.changeValue {
    display: flex;
    column-gap: 2rem;
    font-size: 2rem;
    color: #682c0e;
    align-items: center;
}
.changeValue button {
    border: 1px solid #682c0e;
    text-align: center;
    width: 4rem;
    border-radius: 1rem;
    transition: all 0.2s ease;
    cursor: pointer;
    @media only screen and (max-width: 768px) {
        font-size: 2rem;
    }
}
.changeValue button:active {
    transform: scale(0.9);
}
.underline {
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    background-color: black;
}
