.mainMenudiv {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.mainMenudiv h1 {
    font-size: 2rem;
}
.mainMenudiv p {
    font-size: 1.5rem;
}
.mainMenudiv div p:nth-child(1) {
    font-family: "Noto Sans", sans-serif;
    font-style: italic;
}
.mainMenudivp {
    color: #ae431e;
    font-weight: 700;
}
.mainMenubr {
    height: 1px;
    background-color: rgba(0, 0, 0, 0.7);
    margin: 2rem 0;
}
.mainMenudiv form {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
}
.mainMenudiv form label {
    font-size: 2rem;
}
.mainMenudiv form input {
    width: 3rem;
    height: 2rem;
    border: none;
    font-size: 1rem;
    border-radius: 0.5rem;
}
.mainMenudiv form input:active,
.mainMenudiv form input:focus {
    outline: 1px solid #ae431e;
}
.mainMenudiv form div {
    display: flex;
    column-gap: 1rem;
    align-items: center;
}
.mainMenudiv form button {
    margin-left: auto;
    color: white;
    padding: 0.5rem 1rem;
    background-color: #ae431e;
    border-radius: 2rem;
    font-size: 1.5rem;
    border: none;
}
.mainMenudiv form button:active {
    transform: scale(0.9);
}
