.header {
    display: flex;
    justify-content: space-around;
    padding: 2rem 6rem;
    background-color: #86340a;
    align-items: center;
    @media only screen and (max-width: 768px) {
        padding: 2rem;
        justify-content: space-between;
        column-gap: 4rem;
    }
}

.header h1 {
    font-size: 3rem;
    color: white;
}
.header button {
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
    background-color: #682c0e;
    border: none;
    padding: 1rem 2rem;
    border-radius: 4rem;
    color: white;
}
.header button img {
    width: 2rem;
    filter: brightness(0) invert(1);
}
.header button h2 {
    font-size: 1.5rem;
}
.header button p {
    font-size: 1.4rem;
    font-weight: 700;
    padding: 0.5rem 1rem;
    background-color: #86340a;
    border-radius: 1.5rem;
}
