.mainheading {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 70rem;
    margin: 0 auto;
    padding: 2rem;
    position: relative;
    color: white;
    background-color: #191a19;
    z-index: 2;
    row-gap: 1rem;
    border-radius: 1rem;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
    @media only screen and (max-width: 768px) {
        width: 70vw;
    }
}
.mainheading h1 {
    font-size: 3rem;
}
.mainheading p {
    font-size: 1.5rem;
    font-weight: 400;
}
.mainheading p:nth-child(3) {
    padding: 2rem;
}
