.main {
    position: relative;
    background-image: url("https://images.unsplash.com/photo-1546833999-b9f581a1996d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80");
    background-position: top;
    background-size: 100% 50rem;
    background-repeat: no-repeat;
    min-height: 90vh;
    padding-top: 10rem;
    display: flex;
    flex-direction: column;
    row-gap: 5rem;
    padding: 10rem 0 10rem 0;
}
.mainrelative {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #0f0f0f;
    z-index: 1;
    top: 0;
    clip-path: polygon(0 41%, 100% 10%, 100% 100%, 0% 100%);
    @media only screen and (max-width: 768px) {
        clip-path: polygon(0 41%, 100% 20%, 100% 100%, 0% 100%);
    }
}
