@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

* {
    font-size: 62.5%;
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    @media only screen and (max-width: 1000px) {
        font-size: 50%;
    }
}

html {
    font-family: "Noto Sans JP", sans-serif;
    box-sizing: border-box;
}

body {
    margin: 0;
    min-height: 100vh;
}
