.cart {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    width: 50rem;
    padding: 2rem;
    z-index: 10;
    border-radius: 1rem;
    @media only screen and (max-width: 768px) {
        width: 80vw;
    }
}
.totalName {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
}
.totalName div h1 {
    font-size: 2rem;
}
.totalName div {
    display: flex;
}
.totalName div:nth-child(1) {
    justify-content: space-between;
}
.totalName div:nth-child(2) {
    justify-content: flex-end;
    column-gap: 1rem;
}
.totalName div:nth-child(2) button {
    font-size: 1.5rem;
    border: 1px solid #682c0e;
    padding: 0.5rem 1rem;
    border-radius: 5rem;
    color: #682c0e;
}
.totalName div:nth-child(2) button:nth-child(2) {
    color: white;
    background-color: #682c0e;
}
